//新春抽盲盒活动
const routerConfig = {
  baseUrl: '/TopicApp/2021/manghe/',
  router: [
    {
      path: '/index',
      name: 'index',
      component: resolve => require(['@/pages/BlindBox/Index'], resolve),
      meta: {
        title: '开盲盒 攒车标'
      }
    },
    {
      path: '/duijiang',
      name: 'cashprize',
      component: resolve => require(['@/pages/BlindBox/CashPrize'], resolve),
      meta: {
        title: '兑奖中心'
      }
    },
    {
      path: '/jiangpin',
      name: 'jiangpin',
      component: resolve => require(['@/pages/BlindBox/MyPrize'], resolve),
      meta: {
        title: '我的奖品'
      }
    },
    {
      path: '/kaijiangjieguo',
      name: 'kaijiangjieguo',
      component: resolve => require(['@/pages/BlindBox/OpenBlindBoxResult'], resolve),
      meta: {
        title: '开盲盒结果'
      }
    },
    {
      path: '/renwu',
      name: 'renwu',
      component: resolve => require(['@/pages/BlindBox/TaskList'], resolve),
      meta: {
        title: '任务列表'
      }
    },
    {
      path: '/guize',
      name: 'guize',
      component: resolve => require(['@/pages/BlindBox/Rule'], resolve),
      meta: {
        title: '活动规则 '
      }
    }
  ]
}
export default routerConfig;


